import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button, Modal, ModalBody, ModalHeader, ModalFooter, Input } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class Organization extends Component {
    constructor(props) {
        super(props);

        this.state = {
            opportunity: {},
            logo: '',
        };
    }

    componentDidMount(){
    }

    render() {
        return (
            <Card>
                <CardHeader>
                    <img src={this.props.opportunity.logo}/><b> {this.props.opportunity.name}</b>
              </CardHeader>
                <CardBody>
                    <Table hover bordered striped responsive size="sm">
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Description</th>
                                <th>Link</th>
                                <th>Contact Details</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr key={this.props.opportunity._id}>
                                <td>{this.props.opportunity.name}</td>
                                <td>{this.props.opportunity.description}</td>
                                <td><a href={this.props.opportunity.link}>{this.props.opportunity.link}</a></td>
                                <td>{this.props.opportunity.hotline}</td>
                            </tr>
                        </tbody>
                    </Table>
                </CardBody>
            </Card>
        );
    }
}

export default Organization;