import React, { Component } from 'react';
import { Col, Row, } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
import Organization from './Organization';
const cookies = new Cookies();

class Portal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opportunities: [],
      isLoggedIn: false,
      isAdmin: true,
    };

    this.fetchOrganizations = this.fetchOrganizations.bind(this);
  }
  
  componentWillMount() {
    if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
      this.setState({
        isLoggedIn: true
      });
    }
    this.fetchOrganizations();
  }

  componentDidMount(){
    
  }

  fetchOrganizations() {
    fetch('http://localhost:3001/getOrganizations?token=' + cookies.get('token'))
      .then(response => response.json())
      .then(resData => {
        console.log(resData)
        if (resData.response === 200) {
          this.setState({
            opportunities: resData.data
          });
        } else {
          alert("Error fetching orgs")
        }
      });
  }

  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="animated fadeIn">
          <Row>
            <Col>
                {this.state.opportunities.length > 0 ?
                <div>
                <Organization opportunity={this.state.opportunities[0]} logo={this.state.opportunities[0].logo} />
                <Organization opportunity={this.state.opportunities[1]} logo={this.state.opportunities[1].logo} />
                </div>
                : null}
            </Col>
          </Row>
        </div>
      );
    } else {
      return <Redirect to='/login' />
    }
  }
}

export default Portal;